import { Button, Navbar, Image, Nav, Form, Modal } from "react-bootstrap";
import { useState } from "react";

const Header = () => {
  const [showSI, setShowSI] = useState(false);
  const [showSU, setShowSU] = useState(false);
  const handleCloseSI = () => setShowSI(false);
  const handleShowSI = () => setShowSI(true);
  const handleCloseSU = () => setShowSU(false);
  const handleShowSU = () => setShowSU(true);
  return (
    <>
      <Navbar bg="dark" variant="dark">
        <Navbar.Brand href="#home">CNU Library</Navbar.Brand>
        <Nav className="mr-auto">
          <Nav.Link href="#">Home</Nav.Link>
          <Nav.Link href="#">about</Nav.Link>
          <Nav.Link href="#">Contact</Nav.Link>
        </Nav>
        <Form>
          <Button
            style={{ margin: "0 10px" }}
            variant="dark"
            onClick={handleShowSI}
          >
            Sign in
          </Button>
          <Button variant="dark">Sign up</Button>
        </Form>
      </Navbar>

      <Modal show={showSI} onHide={handleCloseSI}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Modal.Body>Woohoo, you're reading this text in a modal!</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseSI}>
            Close
          </Button>
          <Button variant="primary" onClick={handleCloseSI}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>

      <Modal show={showSU} onHide={handleCloseSU}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Modal.Body>Woohoo, you're reading this text in a modal!</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseSU}>
            Close
          </Button>
          <Button variant="primary" onClick={handleCloseSU}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
export default Header;
